/** Ultra Light */
@font-face {
  font-family: "SFProText";
  font-weight: 100;
  src: url("https://applesocial.s3.amazonaws.com/assets/styles/fonts/sanfrancisco/sanfranciscodisplay-ultralight-webfont.woff");
}

/** Thin */
@font-face {
  font-family: "SFProText";
  font-weight: 200;
  src: url("https://applesocial.s3.amazonaws.com/assets/styles/fonts/sanfrancisco/sanfranciscodisplay-thin-webfont.woff");
}

/** Regular */
@font-face {
  font-family: "SFProText";
  font-weight: 400;
  src: url("https://applesocial.s3.amazonaws.com/assets/styles/fonts/sanfrancisco/sanfranciscodisplay-regular-webfont.woff");
}

/** Medium */
@font-face {
  font-family: "SFProText";
  font-weight: 500;
  src: url("https://applesocial.s3.amazonaws.com/assets/styles/fonts/sanfrancisco/sanfranciscodisplay-medium-webfont.woff");
}

/** Semi Bold */
@font-face {
  font-family: "SFProText";
  font-weight: 600;
  src: url("https://applesocial.s3.amazonaws.com/assets/styles/fonts/sanfrancisco/sanfranciscodisplay-semibold-webfont.woff");
}

/** Bold */
@font-face {
  font-family: "SFProText";
  font-weight: 700;
  src: url("https://applesocial.s3.amazonaws.com/assets/styles/fonts/sanfrancisco/sanfranciscodisplay-bold-webfont.woff");
}

* {
  font-family: "SFProText"
}

body {
  font-family: 'SFProText';
  margin: 0;
  padding: 0;
  background: #000;
}

main {
  flex: auto;
  padding-bottom: 150px;
}

footer {
  position: fixed;
  left: 0;
  right: 0;
  bottom: 0;
}

code,
pre {
  color: #fff;
  font-style: italic;
  padding: 10px;
}

.container {
  position: relative;
  box-sizing: border-box;
}

.box {
  width: 100vw;
  height: auto;
  position: absolute;
  box-sizing: border-box;
  top: 0;
  left: 0;
}

.stack-top {
  z-index: 9;
  box-sizing: border-box;
}

.circle38 {
  width: 38px;
  height: 38px;
  -moz-border-radius: 19px;
  -webkit-border-radius: 19px;
  border-radius: 19px;
}

.circle57 {
  width: 57px;
  height: 57px;
  -moz-border-radius: 29px;
  -webkit-border-radius: 29px;
  border-radius: 29px;
}

.good {
  color: #34C759;
}

.bad {
  color: #FF3B30;
}

.py-5 {
  padding-top: 5px;
  padding-bottom: 5px;
}

.p-8 {
  margin: 8px;
}

.my-16 {
  margin-top: 16px;
  margin-bottom: 16px;
}

.my-8 {
  margin-top: 8px;
  margin-bottom: 8px;
}

.mb-24 {
  margin-bottom: 24px;
}

.screen {
  background-color: black;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

.flex_container {
  display: -webkit-inline-flex;
  display: inline-flex;
  width: 100%;
}

.flex_justify_space_between {
  justify-content: space-between;
  align-items: center;
}

.flex_justify_space_between_top {
  justify-content: space-between;
  align-items: normal;
}

.flex_justify_start {
  justify-content: flex-start;
  align-items: normal;
}

.dispaly_inline_block {
  display: inline-block;
}

/* ACCORDION  */
.accordion_wrap {
  padding: 16px;
}

.accordion_item {
  color: #FFFFFF;
  font-family: 'Roboto';
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 22px;
  background-color: #1C1C1C;
  padding: 13px 14px;
}

.top_radius {
  border-radius: 13px 13px 0 0;
}

.accordion_item__line {
  border-bottom: 0.5px solid rgba(108, 108, 108, 0.5);
}

.accordion_item__rad {
  border-radius: 13px 13px 13px 13px;
}

.accordion_item__top {
  border-radius: 13px 13px 0 0;
}

.accordion_item__bottom {
  border-radius: 0 0 13px 13px;
}

/* TEXT ALIGN */
.align_start {
  text-align: start;
}

.align_end {
  text-align: end;
}

/* IMG  */
.circle {
  border-radius: 50%;
}

.w38 {
  width: 38px;
}

.w57 {
  width: 57px;
}

.w33prc {
  width: 33%;
}

.w100prc {
  width: 100%;
}

.bg_gray {
  background-color: #6C6C6C;
}

.bg_dark {
  background-color: #000;
}

.bg_block {
  background-color: #1C1C1C;
  padding: 1em;
  box-sizing: border-box;
  width: 100%;
  margin: 0;
}

.bg_block_top {
  background-color: #1C1C1C;
  padding: 1em 1em 0 1em;
  box-sizing: border-box;
  width: 100%;
  margin: 0;
}

/* LOGO */
.logo_qoip {
  font-family: SFProText;
  color: #FFFFFF;
  font-weight: 600;
  font-size: 17px;
  line-height: 22px;
  text-align: center;
}

.logo_bot {
  font-family: 'SFProText';
  color: #6C6C6C;
  font-weight: 400;
  font-size: 12px;
  line-height: 16px;
  text-align: center;
}

/* TG USER  */
.tg_user {
  color: #FFFFFF;
  font-family: 'Roboto';
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 19px;
  padding-left: 12px;
  padding-bottom: 4px;
}

.tg_user_login {
  color: #6A6A90;
  font-family: 'Roboto';
  font-style: normal;
  font-weight: 400;
  font-size: 13px;
  line-height: 15px;
  padding-left: 12px;
}

.message_login {
  color: #FFFFFF;
  font-family: 'Roboto';
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 19px;
  padding-left: 12px;
}

.message_border_bottom .message_text {
  margin-top: 6px;
  margin-bottom: 12px;
  width: 90%;
  text-align: justify;
  overflow-wrap: break-word;
  word-wrap: break-word;
  word-break: break-all;
  line-break: auto;
