.message_text {
  color: #6C6C6C;
  font-family: 'Roboto';
  font-style: normal;
  font-weight: 400;
  font-size: 15px;
  line-height: 18px;
  padding-left: 12px;
}

.message_text i {
  color: #fff;
  text-decoration: 1px underline wavy #fff;
}

.message_time {
  color: #6A6A90;
  font-family: 'Roboto';
  font-style: normal;
  font-weight: 400;
  font-size: 13px;
  line-height: 15px;
}

.message_border_bottom {
  border-bottom: 0.35px solid #6C6C6C;
  margin-bottom: 6px;
}

.messages_post {
  margin-top: 16px;
  padding-left: 16px;
  padding-right: 16px;
}

/* BUTTONS  */
.button_link {
  color: #6A6A90;
  text-decoration: none;
  background-color: transparent;
  border: none;
  font-weight: 400;
  font-size: 17px;
  line-height: 22px;
}

.button {
  color: #ffffff;
  border: none;
  background: #6A6A90;
  border-radius: 6px;
  font-family: Roboto;
  font-size: 17px;
  font-weight: 500;
  line-height: 18px;
  text-align: center;
  width: 100%;
  padding: 15px 0 15px 0;
  box-sizing: border-box;
}

.find_block {
  background-color: #1C1C1C;
}


.footer_block button {
  margin-top: 6px;
  margin-bottom: 6px;
}

.footer_block {
  border-radius: 13px 13px 0 0;
  background-color: #1C1C1C;
  padding-top: 4px;
  padding-left: 16px;
  padding-right: 16px;
}

.find_input {
  color: #FFFFFF;
  font-family: 'Roboto';
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 22px;
  background: transparent;
  padding: 16px;
  outline: none;
  border: none;
  box-sizing: border-box;
  width: 100%;
}

.gap_div div {
  padding-right: 5px;
}

.disabled {
  color: #6A6A90;
}

.centered-m {
  text-align: center;
}

.centered-m span {
  font-family: "SFProText";
  font-weight: 800;
  font-size: 40px;
  line-height: 57px;
}

.centered-alert {
  color: #6C6C6C;
  display: flex;
  justify-content: center;
  align-items: center;
}

.centered {
  color: #6C6C6C;
  display: flex;
  justify-content: center;
  align-items: center;
  padding-top: 5vh;
  padding-bottom: 5vh;
  min-height: 25vh;
}

.alert {
  background-color: #1C1C1C;
  color: #FFF;
  width: 90vw;
  border-radius: 13px;
  margin: 1em 0;
}

.alert-head {
  display: flex;
  justify-content: end;
  align-items: center;
  padding: 0.5em 1em;
}

.alert-success {
  border: 2px solid #34C759;
}

.alert-wrong .alert-head {
  background-color: #FF3B30;
}

.alert-success .alert-head {
  background-color: #34C759;
}

.alert-wrong {
  border: 2px solid #FF3B30;
}


.alert-head {
  border-radius: 10px 10px 0 0;
}

.alert-body {
  text-align: justify;
  font-weight: 500;
  padding: 10px;
}

/* loader spin  */
.loader {
  width: 48px;
  height: 48px;
  border: 5px solid #FFF;
  border-bottom-color: transparent;
  border-radius: 50%;
  display: inline-block;
  box-sizing: border-box;
  animation: rotation 1s linear infinite;
}

.load-wrapp {
  display: inline-block;
  width: 100%;
  text-align: center;
  width: 5px;
}

.load-wrapp:last-child {
  margin-right: 0;
}

.bar {
  float: left;
  width: 15px;
  height: 5px;
  border-radius: 2px;
  background-color: #6C6C6C;
}

.load-10 .bar {
  animation: loadingJ 2s cubic-bezier(0.17, 0.37, 0.43, 0.67) infinite;
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

@keyframes loadingJ {

  0%,
  100% {
    transform: translate(0, 0);
  }

  50% {
    transform: translate(80vw, 0);
    width: 50px;
  }
}